import links from '@/components/common/links'

export default {
  name: 'appfooter',
  components: {
    links
  },
  data() {
    return {
      pincha_conf:undefined,
      x91app_conf:undefined
    }
  },
  created() {
    this.pincha_conf = this.$ads_object.text['51pincha'];
    this.x91app_conf = this.$ads_object.text['x91.18r'];
  },
  computed: {
    app_url: function () {
      var app_conf = this.$ads_object.text['pf.app'];
      if (app_conf) {
        return app_conf['url'];
      }
      return undefined;
    }
  }
}
